.title {
  padding: 0;
  margin: 0;
  font-weight: 800;
  line-height: 100%;

  &--large {
    font-size: 77px;
    color: $color-default-white;

    @include vp-1199 {
      font-size: 56px;
    }

    @include vp-767 {
      font-size: 30px;
    }
  }

  &--medium {
    font-size: 35px;
    color: $color-default-black;

    @include vp-1199 {
      font-size: 25px;
    }

    @include vp-767 {
      font-size: 22px;
    }
  }

  &--bg {
    font-size: 55px;
    color: rgba($color-default-black, 0.03);
    position: absolute;
    z-index: -1;

    @include vp-1199 {
      font-size: 40px;
    }

    @include vp-767 {
      font-size: 26px;
    }
  }

  &--small {
    font-size: 21px;
    color: $color-default-black;
  }
}
